/**
 * @file TestCard.js
 * @description A card component that displays test information with interactive features.
 * It shows test details, handles completion toggling, and can open test links in a new tab.
 * 
 * @module components/TestCard
 * @requires react
 * @requires framer-motion
 * @requires ../context/HandlerContext
 */

import React from 'react';
import { useHandlers } from '../context/HandlerContext';
import { motion } from 'framer-motion';

/**
 * @typedef {Object} Test
 * @property {string} title - The title of the test
 * @property {string} [link] - Optional URL to the test (must start with http:// or https://)
 * @property {boolean} [completed=false] - Whether the test has been marked as completed
 * @property {string} [date] - Optional date of the test
 * @property {string} [notes] - Optional notes about the test
 */

/**
 * TestCard Component
 * 
 * @component
 * @param {Object} props - Component props
 * @param {Test} props.test - The test data to display
 * @param {string} props.subject - The subject this test belongs to
 * @param {boolean} [props.isEditMode=false] - Whether the card is in edit mode
 * @param {Function} [props.onDelete] - Callback function when delete button is clicked
 * @returns {React.ReactElement} A card element displaying test information
 * 
 * @example
 * <TestCard 
 *   test={{
 *     title: 'Chapter 1 Quiz',
 *     link: 'https://example.com/quiz',
 *     completed: false
 *   }}
 *   subject="Mathematics"
 *   isEditMode={false}
 *   onDelete={() => console.log('Delete test')}
 * />
 */
const TestCard = React.memo(({ test, subject, isEditMode = false, onDelete }) => {
    const handlers = useHandlers();
    /**
     * Handles the card click event to open the test link in a new tab.
     * Only activates if not in edit mode and if the link is valid.
     * 
     * @private
     */
    const handleCardClick = () => {
        // Allow clicking the card to open a link only if not in edit mode
        if (!isEditMode && test.link && test.link.startsWith('http')) {
            window.open(test.link, '_blank');
        } else if (test.link) {
            // Log a warning for developers if the link is malformed
            console.warn(`Invalid link for ${test.title}: ${test.link}. Must start with http:// or https://`);
        }
    };

    /**
     * Handles the checkbox click to toggle the test's completion status.
     * Stops event propagation to prevent triggering the card's click handler.
     * 
     * @private
     * @param {React.MouseEvent} e - The click event
     */
    const handleCheckboxClick = (e) => {
        // Prevent the card's click event from firing when the checkbox is clicked
        e.stopPropagation();
        handlers.toggleTestCompletion(subject, test.title, !test.completed);
    };

    // Define base classes for the card for consistency
    const baseCardClasses = 'relative p-4 rounded-lg flex justify-between items-center transition-all duration-300';
    
    // Define specific classes for the completed state
    const completedClasses = 'bg-green-100';
    
    // Define specific classes for the default (incomplete) state
    const defaultClasses = 'bg-gray-50';
    
    // Add hover effects only if the card has a link and is not in edit mode
    const linkClasses = test.link && !isEditMode ? 'cursor-pointer hover:shadow-md hover:-translate-y-1' : '';

    /**
     * Combined CSS classes for the card element
     * @type {string}
     * @private
     */
    const cardClasses = `${baseCardClasses} ${test.completed ? completedClasses : defaultClasses} ${linkClasses}`;

    return (
        <div
            id={`trophy-test-item-${subject}-${test.title}`}
            className={cardClasses}
            onClick={handleCardClick}
        >
            <div className="flex items-center flex-grow min-w-0">
                {/* The interactive checkbox for toggling completion status */}
                <motion.button
                    onClick={handleCheckboxClick}
                    className={`flex-shrink-0 w-6 h-6 rounded-full border-2 flex items-center justify-center mr-4`}
                    aria-label={test.completed ? "Mark as incomplete" : "Mark as complete"}
                    whileTap={{ scale: 0.85 }}
                    animate={{
                        scale: test.completed ? 1.1 : 1,
                        backgroundColor: test.completed ? '#22c55e' : '#ffffff00',
                        borderColor: test.completed ? '#22c55e' : '#d1d5db'
                    }}
                    transition={{ type: 'spring', stiffness: 400, damping: 15 }}
                >
                    {test.completed && (
                        <motion.svg 
                            initial={{ scale: 0, rotate: -90 }}
                            animate={{ scale: 1, rotate: 0 }}
                            transition={{ type: 'spring', stiffness: 500, damping: 20 }}
                            xmlns="http://www.w3.org/2000/svg" 
                            className="h-4 w-4 text-white" 
                            fill="none" 
                            viewBox="0 0 24 24" 
                            stroke="currentColor"
                        >
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                        </motion.svg>
                    )}
                </motion.button>
                
                {/* Text content with conditional styling for better readability */}
                <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                        <h4 className={`font-semibold truncate ${test.completed ? 'text-gray-800' : 'text-gray-900'}`}>{test.title}</h4>
                    </div>
                    <p className={`text-sm mt-1 ${test.completed ? 'text-gray-600' : 'text-gray-600'}`}>{test.mcqs} MCQs</p>
                    
                </div>
            </div>
            
            {/* Controls for editing and navigation */}
            <div className="flex-shrink-0 ml-2">
                {isEditMode ? (
                    <button
                        className="p-1.5 rounded-full text-gray-400 hover:text-red-500 hover:bg-red-100 transition-colors duration-200"
                        onClick={(e) => { e.stopPropagation(); onDelete(subject, test.title); }}
                        aria-label="Delete test"
                    >
                         <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M3 6h18"></path><path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"></path><path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"></path><line x1="10" y1="11" x2="10" y2="17"></line><line x1="14" y1="11" x2="14" y2="17"></line></svg>
                    </button>
                ) : (
                    test.link && <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="text-gray-400"><polyline points="9 18 15 12 9 6"></polyline></svg>
                )}
            </div>
        </div>
    );
});

export default TestCard;